import { useState, useEffect, useCallback } from 'react'
import { useInView } from 'react-intersection-observer'
import useAsync from '../../../hooks/useAsync'
import useFilters from '../../../hooks/useFilters'
import { getApplicationColumns } from '../../../utils/columns'
import getFilters from '../api/getFilters'
import getStudentProfile from '../api/getStudentProfile'
import getApplications from './api/getApplications'
import patchApplicationStatus from './api/patchApplicationStatus'
import { API_APPLICATION_GET_FILTERS } from '../../../utils/constants'
import createStatusesArray from '../../../utils/create-statuses-array'

const defaultStudent = { id: undefined }

function useApplications() {
  const [exportDialogOpen, setExportDialogOpen] = useState(false)
  const [editPopupOpen, setEditPopupOpen] = useState(false)
  const [studentToEdit, setStudentToEdit] = useState(defaultStudent)
  const hook = useFilters()
  // const [applications, _setApplications] = useState([])
  const [applications, setApplications] = useState([])
  const [isLoading, setIsLoading] = useState(false)
  const [filtersJSON, setFiltersJSON] = useState({})
  const { run } = useAsync({
    data: [],
    error: null,
  })
  const [rowsCount, setRowsCount] = useState(undefined)
  // const { ref, inView, entry } = useInView({
  //   threshold: 0.5,
  // })

  // const setApplications = (apps) => _setApplications((prev) => [...prev, ...apps])

  const patchStatus = (...args) => run(patchApplicationStatus(...args, setIsLoading, [applications, setApplications]))

  useEffect(() => {
    run(getFilters(setFiltersJSON, API_APPLICATION_GET_FILTERS))
  }, [])

  useEffect(() => {
    if (hook.filters.start_date) run(getApplications(setIsLoading, setApplications, hook.filters, setRowsCount))
  }, [hook.filters])

  useEffect(() => {
    if (!editPopupOpen && studentToEdit?.id) {
      run(getApplications(setIsLoading, setApplications, hook.filters, setRowsCount))
      setStudentToEdit(defaultStudent)
    }
  }, [editPopupOpen])

  const handleDownload = useCallback(() => setExportDialogOpen(true), [setExportDialogOpen])

  const handleCellClick = (params, event) => {
    // eslint-disable-next-line no-param-reassign
    event.defaultMuiPrevented = true
    const student = { ...params?.row, id: params?.row?.profile_id }
    run(getStudentProfile(student, setStudentToEdit, setEditPopupOpen, setIsLoading))
  }

  const columns = getApplicationColumns(
    patchStatus,
    filtersJSON?.courses,
    createStatusesArray(filtersJSON?.statuses),
    // ref,
    () => null,
  )

  // useEffect(() => {
  //   if (inView && Number(hook.filters.page) < Math.ceil(rowsCount / 10)) {
  //     const page = hook.filters.page ? Number(hook.filters.page) + 1 : 1
  //     hook.changeFilters({ ...hook.filters, page })
  //   }
  // }, [inView])

  return {
    editOpenState: [editPopupOpen, setEditPopupOpen],
    exportOpenState: [exportDialogOpen, setExportDialogOpen],
    isLoading,
    studentToEdit,
    filtersJSON,
    columns,
    applications,
    filtersHook: hook,
    handleDownload,
    handleCellClick,
    rowsCount,
  }
}

export default useApplications
