/* eslint-disable no-unused-vars */
import * as React from 'react'

import MenuItem from '@mui/material/MenuItem'
import FormControl from '@mui/material/FormControl'
import Select from '@mui/material/Select'
import FormHelperText from '@mui/material/FormHelperText'

import IconExpandMore from '../../../../assets/Icons/IconExpandMore/IconExpandMore'

import useStyles from './MultipleSelect.style'

const iconComponent = (props) => <IconExpandMore className={`${props.className}`} style={{ right: 11, top: 8 }} />

export default function MultipleSelect({
  isLong,
  title,
  placeholder,
  options,
  valueState,
  error,
  helperText,
  ...props
}) {
  const classes = useStyles()
  const menuProps = {
    classes: {
      paper: classes.paper,
      list: classes.list,
    },
    anchorOrigin: {
      vertical: 'bottom',
      horizontal: 'center',
    },
    transformOrigin: {
      vertical: 'top',
      horizontal: 'center',
    },
    variant: 'menu',
  }

  const [value, setValue] = valueState

  const allOptionsValues = options.map((option) => option.id)

  const isAllSelected = props.multiple && options.length > 0 && value.length === options.length

  const renderValue = (values) => {
    if (!values || values?.length === 0) {
      return <span style={{ color: '#797981' }}>{placeholder}</span>
    }

    if (!props.multiple) {
      return options.find((option) => option.id === values)?.name
    }

    const sortedValues = [...values]
    sortedValues.sort((a, b) => Number(a) - Number(b))
    return sortedValues.map((val) => options.find((o) => o.id === val)?.name).join('; ')
  }

  const handleChange = (event) => {
    const {
      target: { value: currentValue },
    } = event

    if (props.multiple && currentValue.includes('all')) {
      setValue(value.length === options.length ? [] : allOptionsValues)
      return
    }
    setValue(currentValue)
  }

  return (
    <div
      style={{ width: isLong ? '386px' : undefined, maxWidth: isLong ? '386px' : undefined }}
      className={classes.fieldContainer}
    >
      <h2 className={classes.title}>{title}</h2>
      <FormControl className={classes.input}>
        <Select
          MenuProps={menuProps}
          {...props}
          error={error}
          displayEmpty
          placeholder={placeholder}
          value={value}
          renderValue={renderValue}
          IconComponent={iconComponent}
          onChange={handleChange}
        >
          {props.multiple ? (
            <MenuItem value="all" className={isAllSelected && 'Mui-selected'}>
              <span className={classes.item}> </span>
            </MenuItem>
          ) : (
            <MenuItem value="">
              <span className={classes.item}> </span>
            </MenuItem>
          )}

          {options.map((option) => (
            <MenuItem key={option.id} value={option.id}>
              <span className={classes.item}>{option.name}</span>
            </MenuItem>
          ))}
        </Select>
        {!isLong && error && <FormHelperText>{helperText}</FormHelperText>}
        {isLong && <FormHelperText>{error ? helperText : ' '}</FormHelperText>}
      </FormControl>
    </div>
  )
}
